import App              from "App/index.js";
import {renderToString} from "react-dom/server";

const wpiConf = require('App/.wpiConfig.json'),
      fs      = require('fs'),
      express = require('express'),
      tpl     = require('../index.html.tpl');

let currentState;

try {
	currentState = fs.readFileSync('./lastAppState.json');
	currentState = JSON.parse(currentState);
} catch ( e ) {
	currentState = undefined;
}

export default ( server ) => {
	
	server.get(
		'/',
		function ( req, res, next ) {
			App.renderSSR(
				{
					location: req.url,
					state   : currentState,
					tpl
				},
				( err, html, nstate ) => {
					res.send(200, html)
				}
			)
		}
	);
	server.get(
		'/settings',
		function ( req, res, next ) {
			App.renderSSR(
				{
					location: req.url,
					state   : currentState,
					tpl
				},
				( err, html, nstate ) => {
					res.send(200, html)
				}
			)
		}
	);
	server.use(express.static(wpiConf.projectRoot + '/dist'));
	
	server.use("/medias", express.static(wpiConf.projectRoot + '/public'));
	
	server.post('/', function ( req, res, next ) {
		console.log("New state pushed")
		currentState = req.body;
		try {
			fs.writeFileSync('./lastAppState.json', JSON.stringify(req.body));
		} catch ( e ) {
		
		}
		res.send(200, 'ok')
	});
};
